        "content": `Для разогрева пара партий в МТГ. </br></br>Полтора часа читали правила и подготавливались к битве дисков по вахе. Войска быстро поубивались, и если бы не ХХ на кубах орки бы выиграли.</br></br>Прошли первое дело в Тайнах Аркхема. Очень клевая игра. Вроде нашел даже еще два дела не из книги.</br></br>Вечер закончили новинкой - Экосистема. Первая партия - на дафиг мне этот кролик. Вторая партия - где этот гребанный кролик. 10 кроликов из 10.`,
